.section-header-inline {
  display: flex;
  flex-direction: row;
  align-items: center;

  .section-icon {
    margin-bottom: 0;
  }
  h2 {
    margin: 0 0 0 rem(32);
  }
}


@include bp(mobile) {
  .section-icon {
    width: 50px;
  }
  .section-icon--brain {
    width: 70px;
  }
}

